	module.exports = {"data":{"markdownRemark":{"html":"<h2>jQuery Ajax Get Method</h2>\n<p>Sends an asynchronous http GET request to load data from the server. Its general form is:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>jQuery<span class=\"token punctuation\">.</span><span class=\"token keyword\">get</span><span class=\"token punctuation\">(</span> url <span class=\"token punctuation\">[</span><span class=\"token punctuation\">,</span> data <span class=\"token punctuation\">]</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">,</span> success <span class=\"token punctuation\">]</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">,</span> dataType <span class=\"token punctuation\">]</span> <span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<ul>\n<li>url : is the only mandatory parameter. This string contains the adress to which to send the request. The returned data will be ignored if no other parameter is specified</li>\n<li>data : A plain object or string that is sent to the server with the request. </li>\n<li>success : A callback function that is executed if the request succeeds.it takes as an argument the returned data. It is also passed the text status of the response.</li>\n<li>dataType : The type of data expected from the server. The default is Intelligent Guess (xml, json, script, text, html). if this parameter is provided, then the success callback must be provided as well.</li>\n</ul>\n<h4>Examples</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>$<span class=\"token punctuation\">.</span><span class=\"token keyword\">get</span><span class=\"token punctuation\">(</span><span class=\"token string\">'http://example.com/resource.json'</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>category<span class=\"token punctuation\">:</span><span class=\"token string\">'client'</span><span class=\"token punctuation\">,</span> type<span class=\"token punctuation\">:</span><span class=\"token string\">'premium'</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>requests <code>resource.json</code> from the server, sending additional data and ignoring the returned result</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>$<span class=\"token punctuation\">.</span><span class=\"token keyword\">get</span><span class=\"token punctuation\">(</span><span class=\"token string\">'http://example.com/resource.json'</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>category<span class=\"token punctuation\">:</span><span class=\"token string\">'client'</span><span class=\"token punctuation\">,</span> type<span class=\"token punctuation\">:</span><span class=\"token string\">'premium'</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> <span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span>response<span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span> \n     <span class=\"token function\">alert</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"success\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n     <span class=\"token function\">$</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"#mypar\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">html</span><span class=\"token punctuation\">(</span>response<span class=\"token punctuation\">.</span>amount<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>requests <code>resource.json</code> from the server, sending additional data and handling the returned response (json format). This example can be written in this format:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>$<span class=\"token punctuation\">.</span><span class=\"token keyword\">get</span><span class=\"token punctuation\">(</span><span class=\"token string\">'http://example.com/resource.json'</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>category<span class=\"token punctuation\">:</span><span class=\"token string\">'client'</span><span class=\"token punctuation\">,</span> type<span class=\"token punctuation\">:</span><span class=\"token string\">'premium'</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">done</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span>response<span class=\"token punctuation\">)</span><span class=\"token punctuation\">{</span>\n     <span class=\"token function\">alert</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"success\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n     <span class=\"token function\">$</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"#mypar\"</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">html</span><span class=\"token punctuation\">(</span>response<span class=\"token punctuation\">.</span>amount<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h3>jQuery.ajax()</h3>\n<p><code>$.get( url [, data ] [, success ] [, dataType ] )</code> is a shorthand Ajax function, equivalent to:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>$<span class=\"token punctuation\">.</span><span class=\"token function\">ajax</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n url<span class=\"token punctuation\">:</span> url<span class=\"token punctuation\">,</span>\n data<span class=\"token punctuation\">:</span> data<span class=\"token punctuation\">,</span>\n success<span class=\"token punctuation\">:</span> success<span class=\"token punctuation\">,</span>\n dataType<span class=\"token punctuation\">:</span> dataType\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p><code>$.ajax()</code> provides way more options that can be found <a href='http://api.jquery.com/jquery.ajax/' target='_blank' rel='nofollow'>here</a> </p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p>For more information, please visit the <a href='https://api.jquery.com/jquery.get/' target='_blank' rel='nofollow'>official website</a> </p>","fields":{"slug":"/jquery/jquery-ajax-get-method/"},"frontmatter":{"title":"jQuery Ajax Get Method"}}},"pathContext":{"slug":"/jquery/jquery-ajax-get-method/"}}

